import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SeccionMultiusosPage } from './seccion-multiusos.page';

const routes: Routes = [
  {
    path: '',
    component: SeccionMultiusosPage,
  },
  {
    path: 'seccion-plataforma',
    loadChildren: () =>
      import('src/app/pages/SST/informacion-actividad/seccion-plataforma/seccion-plataforma.module').then(
        m => m.SeccionPlataformaPageModule
      ),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SeccionMultiusosPageRoutingModule {}
