import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController, Platform } from '@ionic/angular';
import { MaestroCuestionario } from 'src/app/interfaces/maestro-cuestionario';
import { DataQuestionsService } from 'src/app/services/SST/data-questions.service';
import { UtilsService } from 'src/app/services/utils.service';
import { ModalTrabajadoresComponent } from 'src/app/components/modal-trabajadores/modal-trabajadores.component';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';

@Component({
  selector: 'app-seccion-multiusos',
  templateUrl: './seccion-multiusos.page.html',
  styleUrls: ['./seccion-multiusos.page.scss'],
})
export class SeccionMultiusosPage implements OnInit {
  questions: MaestroCuestionario[];
  data: EDGuardarPermisoTrabajoAPP;
  isDataQuestion = false;
  submitted = false;

  constructor(
    private questionService: DataQuestionsService,
    private utils: UtilsService,
    private alertController: AlertController,
    private router: Router,
    public modalController: ModalController,
    private dataService: SaveDataService
  ) {}

  ngOnInit() {
    this.getQuestions();
  }

  verTrabajadores() {
    this.modalController
      .create({
        component: ModalTrabajadoresComponent,
      })
      .then(modal => {
        modal.present();
      });
  }

  private async getQuestions() {
    this.data = this.dataService.getData();
    let isCuestion = this.data['listaMaestroCuestionario'].filter(
      item => item.strTitulo === 'SECCIÓN ESCALERA PORTÁTIL TIPO TIJERA - MULTIUSOS - EXTENSIÓN'
    );
    await this.utils.presentLoading();
    if (isCuestion.length > 0) {
      this.questions = isCuestion;
      this.isDataQuestion = true;
    } else {
      const multi = await this.questionService.getMultiusos();
      this.questions = multi;
    }
    this.utils.dismissLoading();
  }

  async checkQuestions(questions: MaestroCuestionario[]): Promise<boolean> {
    for (const question of questions) {
      if (!question.strRespuesta) {
        await this.mostrarError('Debe responder todas las preguntas');
        return false;
      }
    }
    return true;
  }

  private async mostrarError(message: string) {
    const alert = await this.alertController.create({
      header: 'Alerta',
      message: message,
      buttons: ['OK'],
    });
    alert.present();
  }
  continuar() {
    this.submitted = true;
    this.checkQuestions(this.questions).then(async result => {
      if (result) {
        await this.guardarDatos();
        this.router.navigate([
          '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma',
        ]);
      }
    });
  }

  async guardarDatos() {
    let data = this.dataService.getData();
    if (this.isDataQuestion) {
      let filtro = data['listaMaestroCuestionario'].filter(
        item => item.strTitulo !== 'SECCIÓN ESCALERA PORTÁTIL TIPO TIJERA - MULTIUSOS - EXTENSIÓN'
      );
      data['listaMaestroCuestionario'] = filtro.concat(this.questions);
    } else {
      data['listaMaestroCuestionario'] = [...data['listaMaestroCuestionario'], ...this.questions];
    }

    this.dataService.setData(data);
  }

  back() {
    this.router.navigate(['/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general'], { state: { data: this.data } });
  }
}
